
import { Box, CircularProgress, Typography } from "@mui/material"

export default function Load(props) {

    const { exibirLoad, mensagemLoad } = props

    return (
        <div>
        {exibirLoad ? 
            <Box sx={{ zIndex: '1', width: '100%', height: '100%', position: 'fixed', top: 0, left: 0, display: '-webkit-box' }}>
                <Box sx={{position: 'relative',zIndex: '5', textAlign: 'center', marginTop: '30%', width: '100%', height: '100%'}}>
                    <CircularProgress />
                    <Typography variant='h5'>{mensagemLoad}</Typography>
                </Box>
                <Box sx={{zIndex: '1', position: 'fixed', opacity: '40%', backgroundColor: 'white', width: '100%', height: '100%'}} />
            </Box>
         : null}
        </div>
    )
}