import { Box, Grid, Typography } from "@mui/material";
import { useState } from 'react'
import Controls from "../../components/controls/Controls";
import ModalConfirmacao from "../../components/ModalConfirmacao";
import { Form, useForm } from "../../components/UseForm";

const initialValues = {
    email: '',
    username: '',
    telefone: '',
    cep: '',
    bairro: '',
    cidade: '',
    estado: '',
    numero: '',
    rua: '',
    complemento: ''

}

export default function Perfil(props) {
    const [isAlterar, setIsAlterar] = useState(false)
    const [exibirModal, setExibirModal] = useState(false)

    const validate = (fieldValues = values) => {

    }

    const {
        values,
        setValues,
        handleInputChange,
        handleInputChangeSwitch,
        erros,
        resetForm,
        setErros
    } = useForm(initialValues, true, validate)

    const handleAlterar = () => {
        setIsAlterar(!isAlterar)
    }

    const handleConfirmar = () =>{
        setExibirModal(false)
    }

    const handleSalvar = () => {
        setIsAlterar(!isAlterar)
        setExibirModal(true)
    }
    return (
        <Form>
            <Box sx={{ textAlign: 'left' }}>
                <Typography variant='h5' color='#8e24aa'>Dados de usurio</Typography>
                <Box style={{ borderBottom: '2px solid #8e24aa', width: '100%', marginBottom: '20px' }}></Box>
                <Grid container spacing={3}>
                    <Grid item md={2} sm={6}>
                        <Controls.Input
                            name='username'
                            label='Username'
                            value={values.username}
                            onChange={handleInputChange}
                            error={erros.username}
                            disabled={!isAlterar}
                        />
                    </Grid>
                    <Grid item md={2} sm={6}>
                        <Controls.Input
                            name='email'
                            label='Email'
                            value={values.email}
                            onChange={handleInputChange}
                            error={erros.email}
                            disabled={!isAlterar}
                        />
                    </Grid>
                    <Grid item md={2} sm={6}>
                        <Controls.Input
                            name='telefone'
                            label='Telefone'
                            value={values.telefone}
                            onChange={handleInputChange}
                            error={erros.telefone}
                            disabled={!isAlterar}
                        />
                    </Grid>
                </Grid>
            </Box>
            <Box sx={{ textAlign: 'left', marginTop: '30px' }}>
                <Typography variant='h5' color='#8e24aa'>Endereo</Typography>
                <Box style={{ borderBottom: '2px solid #8e24aa', width: '100%', marginBottom: '20px' }}></Box>
                <Grid container spacing={3}>
                <Grid item md={2} sm={4}>
                        <Controls.Input
                            name='cep'
                            label='CEP'
                            value={values.cep}
                            onChange={handleInputChange}
                            error={erros.cep}
                            disabled={!isAlterar}
                        />
                    </Grid>
                    <Grid item md={4} sm={6}>
                        <Controls.Input
                            name='cidade'
                            label='Cidade'
                            value={values.cidade}
                            onChange={handleInputChange}
                            error={erros.cidade}
                            disabled={!isAlterar}
                        />
                    </Grid>
                    <Grid item md={2} sm={4}>
                        <Controls.Input
                            name='estado'
                            label='Estado'
                            value={values.estado}
                            onChange={handleInputChange}
                            error={erros.estado}
                            disabled={!isAlterar}
                        />
                    </Grid>
                    <Grid item md={4} sm={6}>
                        <Controls.Input
                            name='rua'
                            label='Rua'
                            value={values.rua}
                            onChange={handleInputChange}
                            error={erros.rua}
                            disabled={!isAlterar}
                        />
                    </Grid>
                    <Grid item md={1} sm={6}>
                        <Controls.Input
                            name='numero'
                            label='Nmero'
                            value={values.numero}
                            onChange={handleInputChange}
                            error={erros.numero}
                            disabled={!isAlterar}
                        />
                    </Grid>
                    <Grid item md={3} sm={6}>
                        <Controls.Input
                            name='complemento'
                            label='Complemento'
                            value={values.complemento}
                            onChange={handleInputChange}
                            error={erros.complemento}
                            disabled={!isAlterar}
                        />
                    </Grid>
                </Grid>
            </Box>
            <Box sx={{ textAlign: 'left', marginTop: '30px' }}>
                <Controls.Button
                    text='Alterar'
                    onClick={handleAlterar}
                    disabled={isAlterar}
                />
                <Controls.Button
                    style={{ marginLeft: '10px' }}
                    text='Salvar'
                    disabled={!isAlterar}
                    onClick={handleSalvar}
                />
                <Controls.Button
                    style={{ marginLeft: '10px' }}
                    text='Cancelar'
                    variant='outlined'
                    onClick={handleAlterar}
                    disabled={!isAlterar}
                />
            </Box>

            <ModalConfirmacao 
                exibirModal={exibirModal}
                setExibirModal={setExibirModal}
                handleConfirmar={handleConfirmar}
                texto='Confirmar alterao de informaes?'
            />
        </Form>
    )
}