import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import LogoutIcon from '@mui/icons-material/Logout';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { IconButton, ToggleButton, ToggleButtonGroup, Tooltip } from '@mui/material';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import { useState } from 'react';
import Popup from '../components/Popup';
import digiIcon from '../img/digivice_icon.png';
import pokeIcon from '../img/pokeball_icon.png';
import LoginForm from './login/LoginForm';
import LoginIcon from '@mui/icons-material/Login';
import StoreIcon from '@mui/icons-material/Store';
import { Link } from 'react-router-dom';
import Drawer from '@mui/material/Drawer';
import CarrinhoDrawer from './perfil/pedido/CarrinhoDrawer';
import CadastroForm from './login/CadastroForm';

export default function HeaderBar(props) {

    const [openPopupLogin, setOpenPopupLogin] = useState(false)
    const { alignment, setAlignment, carrinho, setCarrinho } = props
    const [anchor, setAnchor] = useState(false)
    const [openPopupCadastro, setOpenPopupCadastro] = useState(false)


    const handleChange = (event, newAlignment) => {
        if (newAlignment !== null) {
            setAlignment(newAlignment);
        }
    };

    const openLoginPopup = () => {
        setOpenPopupLogin(true)
    }

    const toggleDrawer = (e, open) => {
        e.preventDefault()
        e.stopPropagation()
        setAnchor(open)
    }

    return (
        <Box sx={{ flexGrow: 1 }}>
            <AppBar position="static">
                <Toolbar style={{ justifyContent: 'space-between' }}>
                    <ToggleButtonGroup
                        color="secondary"
                        value={alignment}
                        exclusive
                        onChange={handleChange}
                        aria-label="Produto"
                    >
                        <ToggleButton value="pokemon">
                            <img src={pokeIcon} alt="pokmon icon" style={{ width: '30px', marginRight: '5px' }} />
                            Pokmon
                        </ToggleButton>
                        <ToggleButton value="digimon" >
                            Digimon
                            <img src={digiIcon} alt="digimon icon" style={{ width: '30px', marginLeft: '5px' }} />
                        </ToggleButton>
                    </ToggleButtonGroup>
                    <Box sx={{
                        '& .MuiSvgIcon-root': {
                            '&:hover': {
                                cursor: 'pointer',
                                fill: 'pink',
                            }
                        }
                    }}>
                        <Tooltip title="Cartas">
                            <Link to='/' >
                                <IconButton color="primary" aria-label="upload picture" component="label">
                                    <StoreIcon style={{ fill: 'white' }} />
                                </IconButton>
                            </Link>
                        </Tooltip>

                        <IconButton color="primary" aria-label="upload picture" component="label" onClick={e => toggleDrawer(e, true)} style={{ marginLeft: '20px' }}>
                            <ShoppingCartIcon style={{ fill: 'white' }} />
                        </IconButton>

                        <Tooltip title="Meu perfil">
                            <Link to='/perfil' >
                                <IconButton color="primary" aria-label="upload picture" component="label" style={{ marginLeft: '20px' }}>
                                    <AccountCircleIcon style={{ fill: 'white' }} />
                                </IconButton>
                            </Link>
                        </Tooltip>

                        <Tooltip title="Login">
                            <IconButton color="primary" aria-label="upload picture" component="label" onClick={openLoginPopup} style={{ marginLeft: '20px' }}>
                                <LoginIcon style={{ fill: 'white' }} />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Logout">
                            <IconButton color="primary" aria-label="upload picture" component="label" style={{ marginLeft: '20px' }}>
                                <LogoutIcon style={{ fill: 'white' }} />
                            </IconButton>
                        </Tooltip>
                    </Box>
                </Toolbar>
            </AppBar>

            <Drawer
                anchor={'right'}
                open={anchor}
                onClose={e => { toggleDrawer(e, false) }}
            >
                <CarrinhoDrawer carrinho={carrinho} setAnchor={setAnchor} setCarrinho={setCarrinho} />
            </Drawer>

            <Popup
                maxWidth='sm'
                openPopup={openPopupLogin}
                setOpenPopup={setOpenPopupLogin}
                title={'Login'}
            >
                <LoginForm setOpenPopup={setOpenPopupLogin} setOpenPopupCadastro={setOpenPopupCadastro} />
            </Popup>
            <Popup
                maxWidth='sm'
                openPopup={openPopupCadastro}
                setOpenPopup={setOpenPopupCadastro}
                title={'Cadastro'}
            >
                <CadastroForm setOpenPopupCadastro={setOpenPopupCadastro} />
            </Popup>
        </Box>
    );
}