import { Box, Grid, Typography } from "@mui/material";
import Controls from "../../components/controls/Controls";
import { useState } from 'react'



export default function LoginForm(props) {
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const {setOpenPopupCadastro, setOpenPopup} = props

    function abrirCriarConta(){
        setOpenPopup(false)
        console.log('aqui')
        setOpenPopupCadastro(true)
        setUsername('')
        setPassword('')
    }
    return (
        <Box>
            <Grid container spacing={3}>
                <Grid item md={12}>
                    <Typography>Esqueci minha senha.</Typography>
                </Grid>
                <Grid item md={12}>
                    <Controls.Input
                        name='username'
                        label='Usurio'
                        value={username}
                    />
                </Grid>
                <Grid item md={12}>
                    <Controls.Input
                        name='password'
                        label='Senha'
                        value={password}
                    />
                </Grid>
                <Grid item md={12}>
                    <Controls.Button
                        text='Log in'
                    />
                    <Controls.Button
                        style={{marginLeft: '10px'}}
                        text='Criar conta'
                        variant='outlined'
                        onClick={() => abrirCriarConta()}
                    />
                </Grid>

            </Grid>
        </Box>
    )
}