import { Box } from "@mui/material";
import AdminCartas from "./AdminCartas";
import AdminClientes from "./AdminClientes";
import AdminEdicoes from "./AdminEdicoes";
import AdminPedidos from "./AdminPedidos";
import AdminProdutos from "./AdminProdutos";
import { useState, useEffect } from 'react'
import CartasService from "../../../services/CartasService";
import Load from "../../../components/Load";


export default function Admin(props) {
    //carregar os combos
    const [listaRaridade, setListaRaridade] = useState([])
    const [listaEdicao, setListaEdicao] = useState([])
    const [listaNatureza, setListaNatureza] = useState([])
    const [listaJogo, setListaJogo] = useState([])
    const [showPage, setShowPage] = useState(false)
    

    useEffect(() => {
        consultarCombos()
    }, []);

    async function consultarCombos() {
        await CartasService.consultarCombos().then((response) => {
            console.log(response)
            if (response !== undefined) {
                setListaRaridade(response.raridade.map(item => {
                    return { id: item.raridade_id, value: item.nome }
                }))
                setListaEdicao(response.edicao.map(item => {
                    return { id: item.edicao_id, value: item.nome }
                }))
                setListaNatureza(response.natureza.map(item => {
                    console.log(item)
                    return { id: item.natureza_id, value: item.nome }
                }))
                setListaJogo(response.jogo.map(item => {
                    return { id: item.jogo_id, value: item.nome }
                }))
            }
            setShowPage(true)
        })
    }
    return (
        <Box>
            <Load exibirLoad={!showPage} mensagemLoad='Carregando...'/> 
            {showPage ?
                <Box>
                    <AdminCartas
                        listaEdicao={listaEdicao}
                        listaNatureza={listaNatureza}
                        listaRaridade={listaRaridade}
                        listaJogo={listaJogo}
                    />
                    <AdminProdutos
                        listaJogo={listaJogo}
                    />
                    <AdminEdicoes
                        listaJogo={listaJogo}
                    />
                    <AdminPedidos />
                    <AdminClientes />
                </Box>
                :
                null}
        </Box>
    )
}