import { Box, Grid } from "@mui/material";
import { useEffect, useState } from "react";
import Controls from '../../components/controls/Controls';
import Load from "../../components/Load";
import { Form, useForm } from '../../components/UseForm';
import CartasService from "../../services/CartasService";

const initialValues = {
    id: 0,
    nome: '',
    natureza: '',
    raridade: '',
    edicao: '',
}

export default function FiltroPesquisa(props) {

    const [listaRaridade, setListaRaridade] = useState()
    const [listaEdicao, setListaEdicao] = useState()
    const [listaNatureza, setListaNatureza] = useState()
    const [showPage, setShowPage] = useState(false)

    const validate = (fieldValues = values) => {

    }

    useEffect(() => {
        consultarCombos()
    }, []);

    async function consultarCombos(){
        await CartasService.consultarCombos().then((response) => {
            console.log('return')
            console.log(response)
            if(response!== undefined){
                converterCombos(response)
            }
            setShowPage(true)
        })
    }

    function converterCombos(response){
        setListaRaridade(response.raridade.map(item => {
            return {id: item.raridade_id, value: item.nome}
        }))
        setListaEdicao(response.edicao.map(item => {
            return {id: item.edicao_id, value: item.nome}
        }))
        setListaNatureza(response.natureza.map(item => {
            return {id: item.natureza_id, value: item.nome}
        }))
    }

    const {
        values,
        setValues,
        handleInputChange,
        handleInputChangeSwitch,
        erros,
        resetForm,
        setErros
    } = useForm(initialValues, true, validate)


    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            //addOrEdit(values, resetForm)
        } else {
        }
    }

    return (
        <Box sx={{ paddingBottom: '1%', marginBottom: '2%', borderBottom: '2px solid pink', flexGrow: 1 }}>
            <Load exibirLoad={!showPage} mensagemLoad='Carregando...'/> 
            {showPage?
            
            <Form onSubmit={handleSubmit}>
                <Grid container spacing={2}>
                    <Grid item md={3} xs={6}>
                        <Controls.Input
                            name='nome'
                            label='Nome'
                            value={values.nome}
                            onChange={handleInputChange}
                            error={erros.nome}
                        />
                    </Grid>
                    <Grid item md={3} xs={6}>
                        <Controls.Select
                            name='natureza'
                            label='Natureza'
                            value={values.natureza}
                            onChange={handleInputChange}
                            options={listaNatureza}
                            error={erros.natureza}
                        />
                    </Grid>
                    <Grid item md={3} xs={6}>
                        <Controls.Select
                            name='raridade'
                            label='Raridade'
                            value={values.raridade}
                            onChange={handleInputChange}
                            options={listaRaridade}
                            error={erros.raridade}
                        />
                    </Grid>
                    <Grid item md={3} xs={6}>
                        <Controls.Select
                            name='edicao'
                            label='Edio'
                            value={values.edicao}
                            onChange={handleInputChange}
                            options={listaEdicao}
                            error={erros.edicao}
                        />
                    </Grid>
                </Grid>
            </Form>
             : null}
        </Box>
    )
}