import { Box, Card, Paper, Tooltip, Typography } from "@mui/material"
import { useRef, useState, useEffect } from "react"
import FavoriteIcon from '@mui/icons-material/Favorite';
import StarIcon from '@mui/icons-material/Star';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';

import brFlag from '../img/icon-brazil.png'
import jpFlag from '../img/icon-japan.png'
import usFlag from '../img/icon-usa.png'
import Popup from "./Popup";
import VisualizarCarta from "../page/Cartas/visualizarCarta/VisualizarCarta";
import Notificacao from "./Notificacao";


export default function Carta(props) {
    const [isShown, setIsShown] = useState(false)
    const boxRef = useRef();
    const [carta, setCarta] = useState({ehColecao: false, ehDesejo: false})
    const [x, setX] = useState();
    const [y, setY] = useState();
    const [openPopup, setOpenPopup] = useState(false)
    const [notify, setNotify] = useState({
        isOpen: false,
        message: "",
        type: "",
    })
    const { nome, img, preco, edicao, raridade, idioma, estoque, ehColecao, ehDesejo, pagina, index, natureza, ano, level, descricao, qualidade, cartaInfo } = props

 
    const getPosition = () => {
        const x = (boxRef.current.offsetLeft - 50);
        setX(x);
        const y = (boxRef.current.offsetTop - 80);
        setY(Number(y));
    };

    useEffect(() => {
        window.addEventListener("resize", getPosition);
        if(cartaInfo!==undefined)
            setCarta(cartaInfo)
    }, [cartaInfo]);

    useEffect(() => {
        getPosition();
    }, []);

    const openInPopup = item => {
        setOpenPopup(true)
    }

    const adicionarCarrinho = () => {
        setNotify({
            isOpen: true,
            message: "Item adicionado ao carrinho.",
            type: "success",
        })
    }

    const adicionarColecao = () => {
        setCarta({...carta, ehColecao: !carta.ehColecao})
    }

    const adicionarDesejo = () => {
        setCarta({...carta, ehDesejo: !carta.ehDesejo})
    }

    return (
        <Box style={{ width: '22%', minWidth: '300px' }} key={index}>
            <Card style={{ padding: '10px', marginBottom: '20px' }}>
                <Notificacao notify={notify} setNotify={setNotify} />
                <Box style={{ display: 'flex' }}>
                    {/* {isShown ?
                        <div style={{ cursor: 'pointer', position: 'absolute', left: x, top: y }}
                            onMouseLeave={(e) => { e.stopPropagation(); setIsShown(false) }}
                            onClick={() => { setIsShown(false); openInPopup() }}>
                            <img src={img} alt="image" style={{ position: 'absolute', zIndex: 5, width: '220px' }} />
                        </div>
                        :
                        null
                    } */}
                    <Box
                        style={{ width: 'fit-content', cursor: 'pointer' }}
                        onMouseEnter={() => setIsShown(true)}
                        onClick={() => { setIsShown(false); openInPopup() }}
                    >
                        <img ref={boxRef} src={img} alt="image" height='208px' width='150px' />

                    </Box>
                    <Box sx={{ width: '100%', marginLeft: '20px', marginRight: '20px', display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }}>
                        <Box>
                            <Box sx={{ display: 'flex', zIndex: 0, }}>
                                <Box sx={{ transform: 'translate(0px, 2px)', display: raridade === 'comum' ? 'block' : 'none', width: '20px', height: '20px', fontSize: '14px', fontWeight: 'bold', borderRadius: '50px', backgroundColor: 'black', color: 'white' }}>C</Box>
                                <Box sx={{ transform: 'translate(0px, 2px)', display: raridade === 'incomum' ? 'block' : 'none', width: '20px', height: '20px', fontSize: '14px', fontWeight: 'bold', borderRadius: '50px', backgroundColor: 'gray', color: 'white' }}>U</Box>
                                <Box sx={{ transform: 'translate(0px, 2px)', display: raridade === 'rara' ? 'block' : 'none', width: '20px', height: '20px', fontSize: '14px', fontWeight: 'bold', borderRadius: '50px', backgroundColor: '#f2b91b', color: 'white' }}>R</Box>
                                <Box sx={{ transform: 'translate(0px, 2px)', display: raridade === 'ultraRara' ? 'block' : 'none', width: '20px', height: '20px', fontSize: '14px', fontWeight: 'bold', borderRadius: '50px', backgroundColor: 'blue', color: 'white' }}>UR</Box>
                                <Box sx={{ transform: 'translate(0px, 2px)', display: raridade === 'secretRara' ? 'block' : 'none', width: '20px', height: '20px', fontSize: '14px', fontWeight: 'bold', borderRadius: '50px', backgroundColor: 'purple', color: 'white' }}>SR</Box>
                                <Box sx={{ transform: 'translate(0px, 2px)', display: raridade === 'promo' ? 'block' : 'none', width: '20px', height: '20px', fontSize: '14px', fontWeight: 'bold', borderRadius: '50px', backgroundColor: 'green', color: 'white' }}>P</Box>
                                <Box sx={{ display: 'flex', marginLeft: '5px' }}>
                                    <img src={brFlag} alt="image" style={{ width: '23px', height: '23px', display: idioma === 'BR' ? 'block' : 'none' }} />
                                    <img src={jpFlag} alt="image" style={{ width: '23px', height: '23px', display: idioma === 'JP' ? 'block' : 'none' }} />
                                    <img src={usFlag} alt="image" style={{ width: '23px', height: '23px', display: idioma === 'US' ? 'block' : 'none' }} />
                                </Box>
                            </Box>
                            <Box>
                                <Box onClick={() => { openInPopup() }} sx={{ '&:hover': { cursor: 'pointer', color: 'purple', textShadow: '0 0 1px purple' } }}>
                                    <Typography variant='body1' style={{ fontWeight: 'bold', textAlign: 'left' }}>
                                        {nome}
                                    </Typography>
                                </Box>
                                <Box sx={{ '&:hover': { cursor: 'pointer', color: 'purple', textShadow: '0 0 1px purple' } }}>
                                    <Typography variant='subtitle2' style={{ textAlign: 'left' }}>
                                        {edicao}
                                    </Typography>
                                </Box>
                            </Box>


                        </Box>

                        <Box sx={{
                            '& .MuiSvgIcon-root': {
                                marginRight: '12px',
                                '&:hover': {
                                    cursor: 'pointer',
                                    fill: '#F06292',
                                }
                            }
                        }}>
                            {pagina !== 'loja' ?
                                null
                                :
                                <Box>
                                    <Typography variant='subtitle2' style={{
                                        fontWeight: 'bold', textAlign: 'left', color: 'white', backgroundColor: estoque === '0' ? 'red' : 'purple', borderRadius: '70px',
                                        width: 'fit-content', paddingLeft: '10px', paddingRight: '10px', marginBottom: '15px'
                                    }}>
                                        {estoque} unid.
                                    </Typography>

                                    <Typography style={{ fontSize: '15px', textAlign: 'left', fontWeight: 'bold', display: 'flex' }}>
                                        Preo: <Typography component={'span'} style={{ fontSize: '15px', fontWeight: 'bold', color: 'green' }}>{preco}</Typography>
                                    </Typography>
                                </Box>
                            }
                            <Box sx={{ marginTop: '10px', textAlign: 'left' }}>
                                {pagina !== 'loja' ?
                                    null
                                    :
                                    <Tooltip title="Adicionar ao carrinho">
                                        <AddShoppingCartIcon onClick={() => adicionarCarrinho()} />
                                    </Tooltip>
                                }
                                <Tooltip title="Lista de desejos">
                                    {carta.ehDesejo ?
                                        <FavoriteIcon color='secondary' onClick={() => adicionarDesejo()}/> : <FavoriteIcon onClick={() => adicionarDesejo()}/>
                                    }
                                </Tooltip>
                                <Tooltip title="Minha coleo">
                                    {carta.ehColecao ?
                                        <StarIcon color='primary' onClick={() => adicionarColecao()}/> : <StarIcon onClick={() => adicionarColecao()}/>
                                    }

                                </Tooltip>
                            </Box>
                        </Box>
                    </Box>
                </Box>
            </Card >
            <Popup
                maxWidth='md'
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
                title={nome}
            >
                <VisualizarCarta
                    carta={{
                        img: img,
                        nome,
                        preco,
                        edicao,
                        raridade,
                        idioma,
                        estoque,
                        ehColecao,
                        ehDesejo,
                        natureza,
                        ano,
                        level,
                        descricao,
                        qualidade
                    }}
                />
            </Popup>
        </Box >
    )
}