
import { Box, Stack } from '@mui/material';
import { useEffect, useState } from 'react';
import Controls from "../../components/controls/Controls";
import CartasService from '../../services/CartasService';
import MiniaturaPedido from './pedido/MiniaturaPedido';

export default function PedidoPopup(props) {
    const [pedido, setPedido] = useState({ lista: [] })
    const [exibirPagina, setExibirPagina] = useState(false)
    const { record } = props

    useEffect(() => {
        console.log('useEffect')
        consultarPedido()
    }, []);

    function consultarPedido() {
        console.log('listaTemp')
        let listaTemp = CartasService.consultarPedidoPorId(1)
        console.log(listaTemp)
        setPedido(listaTemp)
        setExibirPagina(true)
    }

    return (
        <Stack spacing={2} marginLeft={6}>
            {pedido.lista.map((item, index) => {
                return (
                    <MiniaturaPedido item={item} key={index}/>
                )
            })}
        </Stack>
    )
}