import { CssBaseline, ThemeProvider } from "@mui/material";
import { blue, pink, purple } from "@mui/material/colors";
import { createTheme } from "@mui/material/styles";
import { useState } from 'react';
import './App.css';
import Page from './page/Page';
import {
	BrowserRouter,
	Routes,
	Route
} from "react-router-dom";

const roxoEscuro = purple[900]
const roxo = purple[600]
const roxoClaro = purple[300]
const rosaEscuro = pink[500]
const rosa = pink[300]
const rosaClaro = pink[100]
const rosaMuitoClaro = '#ffedf3'
const preto = 'black'
const azul = blue[500]
const azulClaro = blue[100]
const branco = 'white'

const temaPokemon = createTheme({

	palette: {
		background: {
			default: rosaMuitoClaro
		},
		primary: {
			light: roxoClaro,
			main: roxo,
			dark: roxoEscuro,
		},
		secondary: {
			light: rosaClaro,
			main: rosa,
			dark: rosaEscuro,
			background: branco
		},
	},
	components: {
		MuiPaper: {
			styleOverrides: {
				root: {
					'&.MuiDialog-paper': {
						width: '100%',
					}
				}
			}
		},
		MuiFormControl: {
			styleOverrides: {
				root: {
					textAlign: 'left',
					width: '100%',
				}
			}
		},
		MuiToggleButton: {
			styleOverrides: {
				root: {
					backgroundColor: rosa,
					"&.Mui-selected": {
						color: preto,
						backgroundColor: rosaClaro,
						"&:hover": {
							color: branco,
							backgroundColor: rosaClaro
						}
					},
					"&:hover": {
						color: branco,
						backgroundColor: rosa
					}
				}
			}
		}
	}
});

const temaDigimon = createTheme({

	palette: {
		background: {
			default: azulClaro
		},
		primary: {
			light: roxoClaro,
			main: roxo,
			dark: roxoEscuro,
		},
		secondary: {
			light: azulClaro,
			main: azul,
			dark: rosaEscuro,
			background: branco
		},
	},
	components: {
		MuiPaper: {
			styleOverrides: {
				root: {
					'&.MuiDialog-paper': {
						width: '100%',
					}
				}
			}
		},
		MuiFormControl: {
			styleOverrides: {
				root: {
					textAlign: 'left',
					width: '100%',
				}
			}
		},
		MuiToggleButton: {
			styleOverrides: {
				root: {
					backgroundColor: rosa,
					"&.Mui-selected": {
						color: preto,
						backgroundColor: rosaClaro,
						"&:hover": {
							color: branco,
							backgroundColor: rosaClaro
						}
					},
					"&:hover": {
						color: branco,
						backgroundColor: rosa
					}
				}
			}
		}
	}
});

function App() {

	const [alignment, setAlignment] = useState('pokemon');

	return (
		<div className="App">
			<header className="App-header" style={{ minHeight: '100%' }}>
				<ThemeProvider theme={alignment==='pokemon'?temaPokemon:temaDigimon}>
					<CssBaseline />
					<BrowserRouter>
						<Routes>
							<Route path="/" element={<Page
								alignment={alignment}
								setAlignment={setAlignment}
								corpo={'loja'} />} />
							<Route path="/perfil" element={<Page
								alignment={alignment}
								setAlignment={setAlignment}
								corpo={'perfil'} />} />
						</Routes>
					</BrowserRouter>
				</ThemeProvider>
			</header>
		</div>
	);
}

export default App;
