function consultarListaDesejo() {
    return consultarCartas().cartas.filter(x => x.ehDesejo)
}

function consultarColecao() {
    return consultarCartas().cartas.filter(x => x.ehColecao)
}


const CartasService = {
    consultarCartas,
    consultarListaDesejo,
    consultarColecao,
    consultarCartasDigimon,
    consultarCombos,
    consultarCliente,
    consultarPedidoPorId,
    consultarPedidos
}


export default CartasService
