import axios from "axios";

const CONSULTAR_CLIENTE = '/cliente/consultar'
const CONSULTAR_CARTA = '/carta/consultar'
const LISTAR_COMBOS = "/utils/lista/combos"

const client = axios.create({
    baseURL: 'https://services-tcg.herokuapp.com/'
});

export async function teste() {
    await client.get("/teste").then((response) => {
        console.log(response)
    });
}

export async function getListarCombos(jogo = 1) {
    return client.get(LISTAR_COMBOS + '?jogo=' + jogo)
}

export async function getCliente(codigo) {
    return client.get(CONSULTAR_CLIENTE + '?id=' + codigo)
}

export async function getCarta(codigo) {
    return client.get(CONSULTAR_CARTA + '?id=' + codigo)
}