import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import { Snackbar, Box, Typography } from "@mui/material";

export default function Notificacao(props) {

    const { notify, setNotify } = props;

    /*
            setNotify({
                isOpen: true,
                message: ErrorMessages.ERRO_EMPREENDIMENTO_REPETIDO,
                type: "error",
            });
             <Notificacao notify={notify} setNotify={setNotify} />
    */
    const handleClose = (event, reason) => {
        if (reason === "clickaway") {
            return;
        }
        setNotify({
            ...notify,
            isOpen: false,
        });
    };

    return (
        <Snackbar
            open={notify.isOpen}
            autoHideDuration={2500}
            anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
            onClose={handleClose}
            style={{ border: notify.type === 'error' ? '2px solid red' : '2px solid #8e24aa', backgroundColor: 'white', padding: '10px', borderRadius: '50px' }}
        >
            <Box sx={{ display: 'flex' }}>
                {notify.type === 'error' ?
                    <ErrorOutlineIcon style={{ fill: 'red' }} />
                    :
                    <CheckCircleOutlineIcon style={{fill: '#8e24aa'}}/>
                }
                <Typography style={{ marginLeft: '5px' }} color={notify.type === 'error' ? 'red' : '#8e24aa'}>{notify.message}</Typography>
            </Box>
        </Snackbar>
    );
}
