import { Box, Tooltip, Typography } from "@mui/material"
import LojaFragmento from "./LojaFragmento"

import FavoriteIcon from '@mui/icons-material/Favorite';
import StarIcon from '@mui/icons-material/Star';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';

import brFlag from '../../../img/icon-brazil.png'
import jpFlag from '../../../img/icon-japan.png'
import usFlag from '../../../img/icon-usa.png'

export default function VisualizarCarta(props) {
    const { carta } = props
    return (
        <Box>
            <Box sx={{
                '& .MuiSvgIcon-root': {
                    marginRight: '12px',
                    '&:hover': {
                        cursor: 'pointer',
                        fill: '#F06292',
                    }
                }
            }}>

                <Tooltip title="Lista de desejos">
                    {carta.ehDesejo ?
                        <FavoriteIcon fontSize='large' color='secondary' /> : <FavoriteIcon fontSize='large'/>
                    }
                </Tooltip>
                <Tooltip title="Minha coleo">
                    {carta.ehColecao ?
                        <StarIcon fontSize='large' color='primary' /> : <StarIcon fontSize='large'/>
                    }

                </Tooltip>
                <Box sx={{ display: 'flex' }}>
                    <img src={carta.img} alt="image" style={{ zIndex: 5, width: '300px' }} />


                    <Box sx={{ marginLeft: '2%', width: '100%' }}>
                        <Typography> Raridade: {carta.raridade} </Typography>
                        <Typography> Edio: {carta.edicao}</Typography>
                        <Typography> Natureza: {carta.natureza}</Typography>
                        <Typography> Ano: {carta.ano}</Typography>
                        <Typography> Level: {carta.level}</Typography>
                        <Typography style={{marginTop: '20px', fontStyle: 'italic'}}> "{carta.descricao}"</Typography>

                    </Box>
                </Box>
            </Box>
            {carta.estoque !== '0' ? <LojaFragmento carta={carta} /> : null}
        </Box>
    )
}