import StoreIcon from '@mui/icons-material/Store';
import { Box, Tooltip, Typography, Stack } from '@mui/material';

import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';

export default function LojaFragmento(props) {
    const { carta } = props
    return (
        <Box style={{ marginTop: '40px' }}>
            <Box style={{display: 'flex', justifyContent: 'space-between'}}>
            <Box style={{ backgroundColor: '#e8e8e1', width: 'fit-content', padding: '8px', borderRadius: '50px', height: '50px' }}>
                <StoreIcon fontSize='large' />
            </Box>
            <Box sx={{padding: '8px',
                    '& .MuiSvgIcon-root': {
                        '&:hover': {
                            cursor: 'pointer',
                            fill: '#F06292',
                        }
                    }
                }}>
                    <Tooltip title="Adicionar ao carrinho">
                        <AddShoppingCartIcon fontSize='large' />
                    </Tooltip>
                </Box>
                </Box>

            <Box sx={{ borderBottom: '2px solid #e8e8e1', marginTop: '5px' }} />
            <Stack spacing={1} sx={{ marginTop: '10px' }}>
                <Typography> Qualidade: {carta.qualidade}</Typography>
                <Typography> Preo: {carta.preco}</Typography>
                <Typography> Idioma: {carta.idioma}</Typography>
                <Typography> Estoque: {carta.estoque}</Typography>

                
            </Stack>
        </Box>
    )
}