import { Box, Grid, Typography } from "@mui/material";
import { useState } from 'react'
import Controls from "../../components/controls/Controls";
import { Form, useForm } from "../../components/UseForm";

const initialValues = {
    username: '',
    cep: '',
    bairro: '',
    cidade: '',
    estado: '',
    numero: '',
    rua: '',

}

export default function GerenciarConta(props) {
    const [isAlterar, setIsAlterar] = useState(false)

    const validate = (fieldValues = values) => {

    }

    const {
        values,
        setValues,
        handleInputChange,
        handleInputChangeSwitch,
        erros,
        resetForm,
        setErros
    } = useForm(initialValues, true, validate)

    const handleAlterar = () => {
        setIsAlterar(!isAlterar)
    }
    return (
        <Form>
            <Box sx={{ textAlign: 'left' }}>
                <Typography variant='h5' color='#8e24aa'>Alterar Senha</Typography>
                <Box style={{ borderBottom: '2px solid #8e24aa', width: '100%', marginBottom: '20px' }}></Box>
                <Grid container spacing={3}>
                    <Grid item md={4} sm={6}>
                        <Controls.Input
                            name='username'
                            label='Username'
                            value={values.username}
                            onChange={handleInputChange}
                            error={erros.username}
                            disabled={!isAlterar}
                        />
                    </Grid>
                </Grid>
            </Box>
            <Box sx={{ textAlign: 'left', marginTop: '30px' }}>
                <Typography variant='h5' color='#8e24aa'>Excluir Conta</Typography>
                <Box style={{ borderBottom: '2px solid #8e24aa', width: '100%', marginBottom: '20px' }}></Box>
                <Grid container spacing={3}>
                <Grid item md={2} sm={4}>
                        <Controls.Input
                            name='cep'
                            label='CEP'
                            value={values.cep}
                            onChange={handleInputChange}
                            error={erros.cep}
                            disabled={!isAlterar}
                        />
                    </Grid>
                </Grid>
            </Box>
        </Form>
    )
}