import { Box, Card, CardContent, Typography } from "@mui/material"



export default function MiniaturaCarrinho(props) {
    const { item } = props
    return (
        <Card >
            <CardContent style={{ display: 'flex' }}>
                <Box>
                    <img src={item.imagem} alt="image" height='150px' width='110px' />
                </Box>
                <Box sx={{ marginLeft: 3 }}>
                    <Typography sx={{ fontSize: 20 }} color="text.secondary" gutterBottom>
                        {item.nome}
                    </Typography>
                    <Typography variant="body2">
                        Valor Unitrio: R$ {item.valor.toFixed(2)}
                    </Typography>
                    <Typography variant="body2">
                        Quantidade: {item.quantidade}
                    </Typography>
                    <Typography variant="body2">
                        Valor Total: R${(item.quantidade * item.valor).toFixed(2)}
                    </Typography>
                </Box>
            </CardContent>
        </Card>
    )
}