/**
 * Function that gets a random number
 * @param {*} min Minimun range value
 * @param {*} max Maximun range value
 * @returns Return random number between min and max
 */
const randomNumber = (min, max, precision) =>{
    return Math.floor(( min + Math.random() * (max - min)) * precision) / precision 
}

function dividirArrayEmGrupos(arr, len){
    var chunks = [], i = 0, n = arr.length;
    while (i < n) {
        chunks.push(arr.slice(i, i += len));
    }
    return chunks;
}


export function temEstoque(lista, checkedEstoque){
    if(checkedEstoque){
        lista = lista.filter(x => Number(x.estoque) > 0)
    }
    return lista
}
export function cartasDesejo(lista, checkedDesejo){
    if(checkedDesejo){
        lista = lista.filter(x => x.ehDesejo)
    }
    return lista
}
export function cartasPossui(lista, checkedColecao){
    if(!checkedColecao){
        lista = lista.filter(x => !x.ehColecao)
    }
    return lista
}



const Utils = {
    randomNumber,
    dividirArrayEmGrupos,
    cartasPossui,
    cartasDesejo,
    temEstoque
}



export default Utils