import { Box, Grid, Typography } from "@mui/material";
import Controls from "../../../components/controls/Controls";
import { Form, useForm } from "../../../components/UseForm";

const initialValues = {
    nome: '',
    estoque: '',
    edicao: '',
    raridade: '',
    natureza: '',
    qualidade: '',
    jogo: '',
    imagem: '',
    descricao: '',
    valor: '',
    qualidade: ''
}
export default function AdminCartas(props) {
    const { listaNatureza, listaRaridade, listaEdicao, listaJogo } = props
    const validate = (fieldValues = values) => {

    }

    const {
        values,
        setValues,
        handleInputChange,
        handleInputChangeSwitch,
        erros,
        resetForm,
        setErros
    } = useForm(initialValues, true, validate)
    
    return (
        <Form>
            <Box sx={{ textAlign: 'left' }}>
                <Typography variant='h5' color='#8e24aa'>Cartas</Typography>
                <Box style={{ borderBottom: '2px solid #8e24aa', width: '100%', marginBottom: '20px' }} />
                <Grid container spacing={3}>
                    <Grid item md={3} sm={6}>
                        <Controls.Input
                            name='nome'
                            label='Nome'
                            value={values.nome}
                            onChange={handleInputChange}
                            error={erros.nome}
                        />
                    </Grid>
                    <Grid item md={3} xs={6}>
                        <Controls.Select
                            name='natureza'
                            label='Natureza'
                            value={values.natureza}
                            onChange={handleInputChange}
                            options={listaNatureza}
                            error={erros.natureza}
                        />
                    </Grid>
                    <Grid item md={3} xs={6}>
                        <Controls.Select
                            name='raridade'
                            label='Raridade'
                            value={values.raridade}
                            onChange={handleInputChange}
                            options={listaRaridade}
                            error={erros.raridade}
                        />
                    </Grid>
                    <Grid item md={3} xs={6}>
                        <Controls.Select
                            name='edicao'
                            label='Edio'
                            value={values.edicao}
                            onChange={handleInputChange}
                            options={listaEdicao}
                            error={erros.edicao}
                        />
                    </Grid>
                    <Grid item md={3} xs={6}>
                        <Controls.Select
                            name='jogo'
                            label='Jogo'
                            value={values.jogo}
                            onChange={handleInputChange}
                            options={listaJogo}
                            error={erros.jogo}
                        />
                    </Grid>
                </Grid>
            </Box>
        </Form >
    )
}