import { Box, FormControlLabel, Switch } from "@mui/material";
import {
    cartasPossui, cartasDesejo, temEstoque
} from "../../util/Utils"
import Controls from "../../components/controls/Controls";
import Utils from "../../util/Utils";
import constantes from "../../util/constantes";

export default function SwitchesCartas(props) {

    const { values, setValues, setListaVisualizacao, listaCartas, initialValues, pagina } = props

    const handleInputChangeEstoque = () => {
        const novoStatus = !values['checkedEstoque']
        setValues({
            ...values,
            checkedEstoque: novoStatus
        })
        let cartas = []
        if (novoStatus) {
            cartas = cartasPossui(cartasDesejo(listaCartas.filter(x => Number(x.estoque) > 0), values.checkedDesejo), values.checkedColecao)
        } else {
            cartas = cartasPossui(cartasDesejo(listaCartas, values.checkedDesejo), values.checkedColecao)
        }

        setListaVisualizacao(Utils.dividirArrayEmGrupos(cartas, constantes.CARD_PER_ROW))
    }

    const handleInputChangeColecao = () => {
        const novoStatus = !values['checkedColecao']
        setValues({
            ...values,
            checkedColecao: novoStatus
        })
        let cartas = []
        if (!novoStatus) {
            cartas = temEstoque(cartasDesejo(listaCartas.filter(x => !x.ehColecao), values.checkedDesejo), values.checkedEstoque)
        } else {
            cartas = temEstoque(cartasDesejo(listaCartas, values.checkedDesejo), values.checkedEstoque)
        }

        setListaVisualizacao(Utils.dividirArrayEmGrupos(cartas, constantes.CARD_PER_ROW))
    }

    const handleInputChangeDesejo = () => {
        const novoStatus = !values['checkedDesejo']
        setValues({
            ...values,
            checkedDesejo: novoStatus
        })
        let cartas = []
        if (novoStatus) {
            cartas = temEstoque(cartasPossui(listaCartas.filter(x => x.ehDesejo), values.checkedColecao), values.checkedEstoque)
        } else {
            cartas = temEstoque(cartasPossui(listaCartas, values.checkedColecao), values.checkedEstoque)
        }

        setListaVisualizacao(Utils.dividirArrayEmGrupos(cartas, constantes.CARD_PER_ROW))
    }

    const resetarSwitch = () => {
        setValues(initialValues)
        setListaVisualizacao(Utils.dividirArrayEmGrupos(listaCartas, constantes.CARD_PER_ROW))
    }


    return (
        <Box sx={{ textAlign: 'left', marginBottom: '25px' }}>
            <Controls.Button
                style={{ marginRight: '15px' }}
                text='Resetar'
                color='primary'
                onClick={() => resetarSwitch()}
            />
            <FormControlLabel control={<Switch checked={values.checkedEstoque} onChange={e => handleInputChangeEstoque(e, 'checkedEstoque')} />} label="Cartas com estoque" />
            {pagina === 'colecao' ?
                null
                :
                <FormControlLabel control={<Switch checked={values.checkedColecao} onChange={e => handleInputChangeColecao(e, 'checkedColecao')} />} label="Incluir cartas que possuo" />
            }
            {pagina === 'listaDesejo' ?
                null
                :
                <FormControlLabel control={<Switch checked={values.checkedDesejo} onChange={e => handleInputChangeDesejo(e, 'checkedDesejo')} />} label="Somente lista de Desejo" />
            }
        </Box>
    )
}