import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import { Box } from "@mui/material";
import Tab from '@mui/material/Tab';
import { useEffect, useState } from 'react';
import GerenciarConta from './perfil/GerenciarConta';
import MeusPedidos from './perfil/MeusPedidos';
import Perfil from './perfil/Perfil';
import CartasService from '../services/CartasService'
import Admin from './perfil/admin/Admin';

export default function PerfilWrapper(props) {

    const [value, setValue] = useState('1');

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    useEffect(() => {
        consultarCliente(1)
    }, []);

    async function consultarCliente(codigo){
        await CartasService.consultarCliente(codigo).then((response) => {
            console.log('return')
            console.log(response)
        })
    }

    return (
        <TabContext value={value}>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                <TabList onChange={handleChange} aria-label="lab API tabs example">
                    <Tab label="Meus Pedidos" value="1" />
                    <Tab label="Informaes" value="2" />
                    <Tab label="Gerenciar Conta" value="3" />
                    <Tab label="Admin" value="4" />
                </TabList>
            </Box>

            <TabPanel value="1">
                <MeusPedidos />
            </TabPanel>
            <TabPanel value="2">
                <Perfil />
            </TabPanel>
            <TabPanel value="3">
                <GerenciarConta />
            </TabPanel>
            <TabPanel value="4">
                <Admin />
            </TabPanel>
        </TabContext>)
}