import { Container } from '@mui/material';
import React, { useEffect, useState } from 'react';
import HeaderBar from './HeaderBar';
import Wrapper from "./Wrapper";
import pokemon_cover from '../img/pokemon_cover.png'
import CartasService from '../services/CartasService';

function Page(props) {
    const { corpo, alignment, setAlignment } = props
    const [carrinho, setCarrinho] = useState(CartasService.consultarPedidoPorId(1).lista)

    useEffect(() => {
        document.title = "TCG site"
    }, []);

    return (
        <Container style={{ position: 'absolute', top: 0, left: 0, minWidth: '100%', padding: '0px' }}>
            {/* <img src={pokemon_cover} alt="image" style={{ width: '100%', height: '150px' }} /> */}
            <HeaderBar
                alignment={alignment}
                setAlignment={setAlignment}
                carrinho={carrinho}
                setCarrinho={setCarrinho}
            />
            <Wrapper corpo={corpo}
                carrinho={carrinho}
                setCarrinho={setCarrinho} />
        </Container>
    )
}

export default Page;
