import { TableCell, TableRow } from "@material-ui/core";
import VisibilityIcon from '@mui/icons-material/Visibility';
import { Box, TableBody, Typography } from "@mui/material";
import { useEffect, useState } from 'react';
import Popup from "../../components/Popup";
import UseTable from "../../components/UseTable";
import CartasService from "../../services/CartasService";
import PedidoPopup from "./PedidoPopup";

const headCells = [
    { id: 'numero', label: 'Nmero' },
    { id: 'data', label: 'Data' },
    { id: 'valorTotal', label: 'Valor Total' },
    { id: 'status', label: 'Status' },
    { id: 'rastreio', label: 'Cdigo de Rastreio', disableSorting: true },
    { id: 'visualizar', label: '', disableSorting: true },
]

export default function MeusPedidos(props) {
    const [records, setRecords] = useState([])
    const [recordForView, setRecordForView] = useState(null)
    const [filterFn, setFilterFn] = useState({ fn: items => { return items } })
    const [openPopup, setOpenPopup] = useState(false)
    const [numPedido, setNumPedido] = useState('')

    useEffect(() => {
        consultarPedidos()
    }, []);

    function consultarPedidos() {
        setRecords(CartasService.consultarPedidos())
    }

    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = UseTable(records, headCells, filterFn);


    const openInPopup = (e, item) => {
        e.preventDefault()
        e.stopPropagation()
        setRecordForView(item)
        setNumPedido(item.numero)
        setOpenPopup(true)
    }


    return (
        <Box sx={{marginLeft: '10%', width: '80%'}}>
            {records.length > 0 ?
                <Box>
                    <TblContainer >
                        <TblHead />
                        <TableBody>
                            {recordsAfterPagingAndSorting().map((item, index) => (
                                <TableRow key={index}>
                                    <TableCell>{'#' + item.numero}</TableCell>
                                    <TableCell>{item.data}</TableCell>
                                    <TableCell>{item.valorTotal}</TableCell>
                                    <TableCell>{item.status}</TableCell>
                                    <TableCell>{item.rastreio}</TableCell>
                                    <TableCell><VisibilityIcon style={{ cursor: 'pointer' }} onClick={e => openInPopup(e, item)} /></TableCell>
                                </TableRow>
                            )
                            )}
                        </TableBody>
                    </TblContainer>
                </Box>
                :

                <Typography sx={{ margin: '30px' }} variant='body2'>Voc no tem nenhum pedido ainda.</Typography>


            }

            <Popup
                maxWidth='md'
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
                title={'Visualizar pedido #' + numPedido}
            >
                <PedidoPopup 
                    record={recordForView}
                />

            </Popup>
        </Box>
    )
}