import { Box, Pagination } from "@mui/material";
import { useEffect, useState } from 'react';
import Carta from "../../components/Carta";
import CartasService from "../../services/CartasService";
import Utils from "../../util/Utils";
import FiltroPesquisa from "./FiltroPesquisa";
import SwitchesCartas from "./SwitchesCartas";
import constantes from "../../util/constantes"

const initialValues = { checkedEstoque: false, checkedColecao: true, checkedDesejo: false }

export default function ListaDesejos(props) {

    const [page, setPage] = useState(1)
    const [count, setCount] = useState(1)
    const [listaCartas, setListaCartas] = useState([])
    const [listaVisualizacao, setListaVisualizacao] = useState([])
    const [values, setValues] = useState(initialValues)

    useEffect(() => {
        let cartas = CartasService.consultarListaDesejo()
        setListaCartas(cartas)
        let grupos = Utils.dividirArrayEmGrupos(cartas, constantes.CARD_PER_ROW)
        setListaVisualizacao(grupos)
        setCount(Number((Number(grupos.length) / constantes.CARD_PER_PAGE).toFixed(0)))
    }, [setListaCartas])

    const handleChangePagination = (event, value) => {
        setPage(value);
    };

    return (
        <Box>
            <SwitchesCartas
                listaCartas={listaCartas}
                values={values}
                setValues={setValues}
                setListaVisualizacao={setListaVisualizacao}
                initialValues={initialValues}
                pagina={'listaDesejo'}
            />
            <Box>
                {listaVisualizacao.slice((page - 1) * constantes.CARD_PER_PAGE, page * constantes.CARD_PER_PAGE).map((lista, i) => {
                    return (
                        <Box key={'desejo' + i} sx={{ display: 'flex', justifyContent: 'space-evenly' }}>
                            {lista.map((item, index) => {
                                return (<Carta
                                    cartaInfo={item}
                                    index={'desejo' + index}
                                    nome={item.nome}
                                    img={item.img}
                                    preco={item.preco}
                                    edicao={item.edicao}
                                    raridade={item.raridade}
                                    idioma={item.idioma}
                                    estoque={item.estoque}
                                    ehColecao={item.ehColecao}
                                    ehDesejo={item.ehDesejo}
                                    pagina='listaDesejo'
                                />)
                            })}
                        </Box>
                    )
                })}
            </Box>

            <Box sx={{
                '& .MuiPagination-ul': {
                    justifyContent: 'center'
                }
            }}>
                <Pagination count={count} color="secondary" page={page} onChange={handleChangePagination} />
            </Box>
        </Box>
    )
}