import { TabPanel, TabList, TabContext } from '@mui/lab';
import { Box, Tab } from "@mui/material";
import { useState } from 'react';
import Loja from "./Cartas/Loja";
import ListaDesejos from "./Cartas/ListaDesejos";
import Colecao from './Cartas/Colecao';
import FiltroPesquisa from './Cartas/FiltroPesquisa';

export default function LojaWrapper(props) {

    const [value, setValue] = useState('1');

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <TabContext value={value}>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                <TabList onChange={handleChange} aria-label="lab API tabs example">
                    <Tab label="Loja" value="1" />
                    <Tab label="Todas as Cartas" value="2" />
                    <Tab label="Minha Coleo" value="3" />
                    <Tab label="Lista de Desejos" value="4" />
                </TabList>
            </Box>

            <TabPanel value="1">
                <FiltroPesquisa />
                <Loja />
            </TabPanel>
            <TabPanel value="2">
                Todas as Cartas
                <Loja />
            </TabPanel>
            <TabPanel value="3">
                <FiltroPesquisa />
                <Colecao />
            </TabPanel>
            <TabPanel value="4">
                <FiltroPesquisa />
                <ListaDesejos />
            </TabPanel>
        </TabContext>)
}