import { Box, Paper } from "@mui/material";
import LojaWrapper from "./LojaWrapper";
import PerfilWrapper from "./PerfilWrapper";

export default function Wrapper(props) {
    const {corpo} = props


    return (
        <Box sx={{ width: '90%', marginLeft: '5%'}}>
            <Paper style={{ margin: '1%'}} >
                {corpo === 'loja'?
                    <LojaWrapper />
                    :
                    null
                }
                {corpo === 'perfil'?
                    <PerfilWrapper />
                    :
                    null
                }
            </Paper>
        </Box>

    )
}