import { Stack } from "@mui/material";
import Controls from "../../../components/controls/Controls";
import MiniaturaCarrinho from "./MiniaturaCarrinho";




export default function CarrinhoDrawer(props){
    const {carrinho, setAnchor} = props

    const fecharDrawer = e => {
        console.log('aqui')
        setAnchor(false)
    }
    console.log(carrinho)

    return(
        <Stack spacing={3} padding={4}>
            <Controls.Button 
                text='Pagamento'
                variant='outlined'
                size='medium'
                onClick={(e) => fecharDrawer(e)}
            />
            {carrinho.map((item, index) => {
                return <MiniaturaCarrinho item={item} key={index}/>
            })}
        </Stack>
    )
}